	module.exports = {"data":{"markdownRemark":{"html":"<p>Stop WordPress before making blogs with Jekyll! I got a fancy word, but finally we have realized the things we realized. (Actually it's not Jekyll but Gatsby Hair dressing fee , React based blogging tool)</p>\n<p>By doing this, you can now update your blog without permission by simply pushing Markdown to GitHub! I also made an English version blog. By pressing the header flag, you can switch between English and Japanese. When making an article, it has a convenient function that Google translates without permission.</p>\n<p>Although this page is still a test version, I would like to completely replace the <a href=\"https://danmaq.com/\">old blog</a> with a prospect of around GW in April.</p>\n<h1>Still remaining problems</h1>\n<ul>\n<li>The article Markdown is a mechanical extraction from WordPress, and there are places where manual adjustment is necessary</li>\n<li>When all the articles are made in blogs, they die in a small part of the Chrome environment. (And the cause unknown)</li>\n</ul>","frontmatter":{"cover":{"childImageSharp":{"responsiveSizes":{"src":"/static/2017-wp-to-jekyll-369dba5e803dac98b14f3166d0574dce-2fd78.png","srcSet":"/static/2017-wp-to-jekyll-369dba5e803dac98b14f3166d0574dce-739b5.png 200w,\n/static/2017-wp-to-jekyll-369dba5e803dac98b14f3166d0574dce-549e8.png 400w,\n/static/2017-wp-to-jekyll-369dba5e803dac98b14f3166d0574dce-2fd78.png 800w,\n/static/2017-wp-to-jekyll-369dba5e803dac98b14f3166d0574dce-5cf57.png 970w","sizes":"(max-width: 800px) 100vw, 800px"}}},"date":"2018-02-21T13:47:00+09:00","strDate":"2018/2/21","redirect":null,"tags":["release","diary","gatsby","jekyll","wordpress"],"title":"Renewal of danmaq web page Notice (It's not Jekyll but Gatsby)","youtube":null}},"site":{"siteMetadata":{"langKeyDefault":"ja","langs":["ja","en"]}}},"pathContext":{"path":"/en/blog/2018/02/21-renewal/","langKey":"en"}}

